* {
  margin: 0;
  padding: 0;
}

body {
  padding: 10px;
}

.hidden {
  border: 2px solid #000;
  display: none !important;
}

.breed-select {
  max-width: 250px;
  margin-bottom: 30px;
}

.cat-info {
  width: 100%;
  max-width: 800px;
  flex-wrap: wrap;
  gap: 15px;
  display: flex;
}

.img-container {
  min-width: 300px;
  flex-basis: 300px;
}

.cat-info img {
  width: 100%;
}

.description {
  min-width: 325px;
  max-width: 480px;
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.loader {
  letter-spacing: 2px;
  font-size: 48px;
  position: relative;
}

.loader:before {
  content: "Loading";
  color: #000;
}

.loader:after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #ff3d00;
  border-radius: 50%;
  margin: auto;
  animation: motion 3s ease-in-out infinite;
  position: absolute;
  top: -70px;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes motion {
  0%, 50%, 100% {
    transform: translateX(0)scale(1);
  }

  25% {
    transform: translateX(-100px)scale(.3);
  }

  75% {
    transform: translateX(100px)scale(.3);
  }
}

